import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ReactTooltip from 'react-tooltip';
import ReactImageFallback from "react-image-fallback";

import './Item.scss';
import * as action from '../../redux/actions';
import { getPinnedCard } from '../../redux/store';

export const Item = ({ id, title, description, image, price }) => {
  const pinnedCard = useSelector(getPinnedCard)
  const dispatch = useDispatch();

  const pinHandler = (id) => {
    (id === pinnedCard)
      ? dispatch(action.unpinProduct())
      : dispatch(action.pinProduct(id))
  }

  return (
    <li className="card" data-qa="card">
      <ReactImageFallback
        src={image}
        fallbackImage="data:image/png;base64,"
        alt="title"
        className="card__image"
        onError={() => console.log(this)}
        />
      <h3 className="card__name">{title}</h3>
      <div className="card__cost">
        <span className="card__value">Price:</span>
        <span className="card__price">${price}</span>
      </div>
      <div className="card__description" data-tip={description}>
        {description}
        <ReactTooltip
          place="top"
          effect="solid"
          multiline={true}
        />
        <span className="card__description_fade-out"></span>
      </div>
      <div className="card__buttons">
        <button
          className="card__pin card__button"
          onClick={() => pinHandler(id)}
        >
          {pinnedCard === id ? 'Unpin' : 'Pin'}
        </button>
        <button
          className="card__delete card__button"
          onClick={() => dispatch(action.deleteProduct(id))}
        >
          Delete
        </button>
      </div>
    </li>
  )
}